.Spinner_spinner_container__3fX62{
  display:flex;
  align-items: center;
  margin:0 auto;
  height:calc(100vh - 333px);
}

.Spinner_loader__2shKb,
.Spinner_loader__2shKb:after {
  border-radius: 50%;
  width: 10em;
  height: 10em;
}
.Spinner_loader__2shKb {
  font-size: 10px;
  position: relative;
  border-top: 1.1em solid rgba(255, 255, 255, 0.2);
  border-right: 1.1em solid rgba(255, 255, 255, 0.2);
  border-bottom: 1.1em solid rgba(255, 255, 255, 0.2);
  border-left: 1.1em solid rgb(233, 121, 121);
  transform: translateZ(0);
  animation: Spinner_load8__TLpDg 1.1s infinite linear;
}
@keyframes Spinner_load8__TLpDg {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}


